.Head {
    color: red;
    box-shadow: 1px 5px greenyellow;
    background-color: rgba(41, 187, 41, 0.938);
  }
  .Question {
    color: steelblue;
    font-family: Arial, Helvetica, sans-serif;
  }
  .input {
    border: 2px solid green;
    width: 90vw;
    height: 10vh;
    box-shadow: 0.2rem 0.2rem 0 var(--color-shadow, currentColor);
  }
  
  .button {
    border-radius: 1px;
    background-color: #f4511e;
    border: none;
    color: #ffffff;
    text-align: center;
    font-size: 28px;
    width: 20vw;
  
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
    border: 2px solid green;
  }
  .button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .button span:after {
    content: "\00bb";
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .button:hover span {
    padding-right: 25px;
  }
  
  .button:hover span:after {
    opacity: 1;
    right: 0;
  }
  